/**
 * 
 * @description Token
 */

import {
    API_CONFIG,
    ApiError,
    ApiResponse,
    ErrorCode,
    getApiUrl,
    getErrorMessage,
    HttpMethod,
    RequestOptions,
    Storage,
} from './common';

/**
 * Token 
 */
const TOKEN_KEY = 'auth_token';
const USER_INFO_KEY = 'user_info';

/**
 * Token 
 */
export class TokenManager {
  /**
   *  Token
   * @returns Token  null
   */
  static getToken(): string | null {
    return Storage.get<string>(TOKEN_KEY);
  }

  /**
   *  Token
   * @param token - Token 
   */
  static setToken(token: string): void {
    Storage.set(TOKEN_KEY, token);
  }

  /**
   *  Token
   */
  static removeToken(): void {
    Storage.remove(TOKEN_KEY);
  }

  /**
   * 
   * @returns  Token
   */
  static isLoggedIn(): boolean {
    return !!this.getToken();
  }

  /**
   * Token
   */
  static clearAuth(): void {
    this.removeToken();
    Storage.remove(USER_INFO_KEY);
  }
}

/**
 * HTTP 
 */
export class HttpClient {
  /**
   * Promise
   * @param timeout - 
   * @returns Promise
   */
  private static createTimeoutPromise(timeout: number): Promise<never> {
    return new Promise((_, reject) => {
      setTimeout(() => {
        reject(new ApiError(ErrorCode.ERROR, ''));
      }, timeout);
    });
  }

  /**
   * 
   * @param options - 
   * @returns Headers 
   */
  private static buildHeaders(options: RequestOptions): HeadersInit {
    const headers: HeadersInit = {
      'Content-Type': 'application/json',
      ...options.headers,
    };

    //  Authorization 
    if (options.requireAuth !== false) {
      const token = TokenManager.getToken();
      if (token) {
        (headers as Record<string, string>)['Authorization'] = `Bearer ${token}`;
      }
    }

    return headers;
  }

  /**
   * 
   * @param response - Fetch Response 
   * @returns 
   */
  private static async handleResponse<T>(response: Response): Promise<T> {
    // JSON
    let data: ApiResponse<T>;
    try {
      data = await response.json();
    } catch (error) {
      throw new ApiError(
        ErrorCode.ERROR,
        '',
        response
      );
    }

    // 
    if (data.code !== ErrorCode.SUCCESS) {
      const errorMessage = data.message || getErrorMessage(data.code);
      const apiError = new ApiError(data.code, errorMessage, data);

      // 
      if (apiError.isAuthError()) {
        TokenManager.clearAuth();
        // 
        window.dispatchEvent(new CustomEvent('auth:expired'));
      }

      throw apiError;
    }

    return data.data as T;
  }

  /**
   *  HTTP 
   * @param url -  URL
   * @param options - 
   * @returns Promise<T>
   */
  static async request<T = any>(
    url: string,
    options: RequestOptions = {}
  ): Promise<T> {
    const {
      requireAuth = true,
      timeout = API_CONFIG.TIMEOUT,
      showLoading = false,
      showError = true,
      ...fetchOptions
    } = options;

    // 
    const requestInit: RequestInit = {
      ...fetchOptions,
      headers: this.buildHeaders({ requireAuth, ...fetchOptions }),
    };

    try {
      // 
      if (showLoading) {
        window.dispatchEvent(new CustomEvent('loading:start'));
      }

      // PromisePromise
      const fetchPromise = fetch(url, requestInit);
      const timeoutPromise = this.createTimeoutPromise(timeout);

      // 
      const response = await Promise.race([fetchPromise, timeoutPromise]);

      // 
      return await this.handleResponse<T>(response);
    } catch (error) {
      // 
      if (error instanceof ApiError) {
        // 
        if (showError) {
          window.dispatchEvent(
            new CustomEvent('error:show', { detail: error.message })
          );
        }
        throw error;
      }

      // 
      const networkError = new ApiError(
        ErrorCode.ERROR,
        '',
        error
      );

      if (showError) {
        window.dispatchEvent(
          new CustomEvent('error:show', { detail: networkError.message })
        );
      }

      throw networkError;
    } finally {
      // 
      if (showLoading) {
        window.dispatchEvent(new CustomEvent('loading:end'));
      }
    }
  }

  /**
   * GET 
   * @param path - API 
   * @param params - 
   * @param options - 
   * @returns Promise<T>
   */
  static async get<T = any>(
    path: string,
    params?: Record<string, any>,
    options?: RequestOptions
  ): Promise<T> {
    const queryString = params
      ? '?' +
        Object.entries(params)
          .filter(([_, v]) => v !== undefined && v !== null && v !== '')
          .map(([k, v]) => `${encodeURIComponent(k)}=${encodeURIComponent(String(v))}`)
          .join('&')
      : '';

    const url = getApiUrl(path) + queryString;
    return this.request<T>(url, {
      method: HttpMethod.GET,
      ...options,
    });
  }

  /**
   * POST 
   * @param path - API 
   * @param data - 
   * @param options - 
   * @returns Promise<T>
   */
  static async post<T = any>(
    path: string,
    data?: any,
    options?: RequestOptions
  ): Promise<T> {
    const url = getApiUrl(path);
    return this.request<T>(url, {
      method: HttpMethod.POST,
      body: data ? JSON.stringify(data) : undefined,
      ...options,
    });
  }

  /**
   * PUT 
   * @param path - API 
   * @param data - 
   * @param options - 
   * @returns Promise<T>
   */
  static async put<T = any>(
    path: string,
    data?: any,
    options?: RequestOptions
  ): Promise<T> {
    const url = getApiUrl(path);
    return this.request<T>(url, {
      method: HttpMethod.PUT,
      body: data ? JSON.stringify(data) : undefined,
      ...options,
    });
  }

  /**
   * DELETE 
   * @param path - API 
   * @param options - 
   * @returns Promise<T>
   */
  static async delete<T = any>(
    path: string,
    options?: RequestOptions
  ): Promise<T> {
    const url = getApiUrl(path);
    return this.request<T>(url, {
      method: HttpMethod.DELETE,
      ...options,
    });
  }

  /**
   * PATCH 
   * @param path - API 
   * @param data - 
   * @param options - 
   * @returns Promise<T>
   */
  static async patch<T = any>(
    path: string,
    data?: any,
    options?: RequestOptions
  ): Promise<T> {
    const url = getApiUrl(path);
    return this.request<T>(url, {
      method: HttpMethod.PATCH,
      body: data ? JSON.stringify(data) : undefined,
      ...options,
    });
  }
}

/**
 * 
 */
export type RequestInterceptor = (
  url: string,
  options: RequestOptions
) => { url: string; options: RequestOptions } | Promise<{ url: string; options: RequestOptions }>;

/**
 * 
 */
export type ResponseInterceptor<T = any> = (
  response: T
) => T | Promise<T>;

/**
 * 
 */
export type ErrorInterceptor = (
  error: ApiError
) => void | Promise<void>;

/**
 * 
 */
export class InterceptorManager {
  private static requestInterceptors: RequestInterceptor[] = [];
  private static responseInterceptors: ResponseInterceptor[] = [];
  private static errorInterceptors: ErrorInterceptor[] = [];

  /**
   * 
   * @param interceptor - 
   */
  static addRequestInterceptor(interceptor: RequestInterceptor): void {
    this.requestInterceptors.push(interceptor);
  }

  /**
   * 
   * @param interceptor - 
   */
  static addResponseInterceptor(interceptor: ResponseInterceptor): void {
    this.responseInterceptors.push(interceptor);
  }

  /**
   * 
   * @param interceptor - 
   */
  static addErrorInterceptor(interceptor: ErrorInterceptor): void {
    this.errorInterceptors.push(interceptor);
  }

  /**
   * 
   */
  static async runRequestInterceptors(
    url: string,
    options: RequestOptions
  ): Promise<{ url: string; options: RequestOptions }> {
    let result = { url, options };
    for (const interceptor of this.requestInterceptors) {
      result = await interceptor(result.url, result.options);
    }
    return result;
  }

  /**
   * 
   */
  static async runResponseInterceptors<T>(response: T): Promise<T> {
    let result = response;
    for (const interceptor of this.responseInterceptors) {
      result = await interceptor(result);
    }
    return result;
  }

  /**
   * 
   */
  static async runErrorInterceptors(error: ApiError): Promise<void> {
    for (const interceptor of this.errorInterceptors) {
      await interceptor(error);
    }
  }
}

//  HTTP 
export default HttpClient;
