import React from 'react';
import '../styles/app.css';
import { TemplateData } from '../utils/validation';

interface PreviewSectionProps {
  templateData: TemplateData;
}

/**
 * 
 * 
 */
const PreviewSection: React.FC<PreviewSectionProps> = ({ templateData }) => {
  /**
   * 
   * @returns Markdown
   */
  const generatePreview = (): string => {
    return `## 
${templateData.taskObjective || '[AI]'}

## AI
${templateData.aiRole || '[AI]'}

## 
${templateData.myRole || '[]'}

## 
${templateData.keyInformation || '[]'}

## 
${templateData.behaviorRules || '[1]\n[2]\n[1]\n[2]'}

## 
${templateData.deliveryFormat || '[MarkdownJSONPPT]'}
`;
  };

  /**
   * 
   * Clipboard API
   */
  const handleCopyPreview = (): void => {
    const previewContent = generatePreview();
    navigator.clipboard.writeText(previewContent)
      .then(() => {
        alert('');
      })
      .catch(err => {
        console.error(':', err);
        alert('');
      });
  };

  return (
    <div className="preview-section">
      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <h2 className="preview-title"></h2>
        <button className="btn btn-secondary" onClick={handleCopyPreview} style={{ minWidth: 'auto', padding: '8px 16px' }}>
          
        </button>
      </div>
      <div className="preview-content">
        {generatePreview()}
      </div>
    </div>
  );
};

export default PreviewSection;
