/**
 * 
 */

// 
export interface TemplateData {
  topic: string;
  taskObjective: string;
  aiRole: string;
  myRole: string;
  keyInformation: string;
  behaviorRules: string;
  deliveryFormat: string;
}

// 
export interface ValidationResult {
  isValid: boolean;
  errors: Record<string, string>;
}

// 
export type FieldName = keyof TemplateData;

/**
 * 
 * @param templateData - 
 * @returns  { isValid: boolean, errors: object }
 */
export const validateTemplateData = (templateData: TemplateData): ValidationResult => {
  const errors: Record<string, string> = {};

  // 
  if (!templateData.topic || templateData.topic.trim() === '') {
    errors.topic = '';
  } else if (templateData.topic.length > 100) {
    errors.topic = '100';
  }

  // 
  if (!templateData.taskObjective || templateData.taskObjective.trim() === '') {
    errors.taskObjective = '';
  }

  // AI
  if (!templateData.aiRole || templateData.aiRole.trim() === '') {
    errors.aiRole = 'AI';
  }

  // 
  if (!templateData.myRole || templateData.myRole.trim() === '') {
    errors.myRole = '';
  }

  // 
  if (!templateData.keyInformation || templateData.keyInformation.trim() === '') {
    errors.keyInformation = '';
  }

  // 
  if (!templateData.behaviorRules || templateData.behaviorRules.trim() === '') {
    errors.behaviorRules = '';
  }

  // 
  if (!templateData.deliveryFormat || templateData.deliveryFormat.trim() === '') {
    errors.deliveryFormat = '';
  }

  return {
    isValid: Object.keys(errors).length === 0,
    errors
  };
};

/**
 * 
 * @param field - 
 * @param value - 
 * @returns null
 */
export const validateField = (field: FieldName, value: string): string | null => {
  switch (field) {
    case 'topic':
      if (!value || value.trim() === '') {
        return '';
      } else if (value.length > 100) {
        return '100';
      }
      return null;
    case 'taskObjective':
      if (!value || value.trim() === '') {
        return '';
      }
      return null;
    case 'aiRole':
      if (!value || value.trim() === '') {
        return 'AI';
      }
      return null;
    case 'myRole':
      if (!value || value.trim() === '') {
        return '';
      }
      return null;
    case 'keyInformation':
      if (!value || value.trim() === '') {
        return '';
      }
      return null;
    case 'behaviorRules':
      if (!value || value.trim() === '') {
        return '';
      }
      return null;
    case 'deliveryFormat':
      if (!value || value.trim() === '') {
        return '';
      }
      return null;
    default:
      return null;
  }
};
