/**
 * 
 * @description API
 */

import HttpClient, { TokenManager } from './auth';
import { Storage } from './common';

/**
 * 
 */
export interface UserInfo {
  /** ID */
  id: number;
  /**  */
  mobile: string;
  /**  */
  email?: string;
  /** normal-admin-vip-VIP */
  user_type?: string;
  /** 1-0-2- */
  user_status?: number;
  /**  */
  created_at?: string;
  /**  */
  updated_at?: string;
}

/**
 * 
 */
export interface RegisterRequest {
  /** 11 1[3-9]\d{9} */
  mobile: string;
  /** 8-16 */
  password: string;
}

/**
 * 
 */
export interface RegisterResponse {
  /**  */
  user: UserInfo;
}

/**
 * 
 */
export interface LoginRequest {
  /**  */
  mobile: string;
  /**  */
  password: string;
}

/**
 * 
 */
export interface LoginResponse {
  /** JWT Token */
  token: string;
  /**  */
  user: UserInfo;
}

/**
 * 
 */
export interface ChangePasswordRequest {
  /**  */
  mobile: string;
  /**  */
  old_password: string;
  /** 8-16 */
  new_password: string;
}

/**
 * 
 */
const USER_INFO_KEY = 'user_info';

/**
 * 
 * @description API
 */
export class UserService {
  /**
   * 
   * @param data - 
   * @returns Promise<UserInfo> 
   * @throws {ApiError} 
   * 
   * @example
   * ```typescript
   * try {
   *   const user = await UserService.register({
   *     mobile: '13800138000',
   *     password: 'Test@123456'
   *   });
   *   console.log(':', user);
   * } catch (error) {
   *   console.error(':', error.message);
   * }
   * ```
   */
  static async register(data: RegisterRequest): Promise<UserInfo> {
    const response = await HttpClient.post<UserInfo>(
      '/user/register',
      data,
      {
        requireAuth: false, // 
        showLoading: true,
        showError: true,
      }
    );
    return response;
  }

  /**
   * 
   * @param data - 
   * @returns Promise<LoginResponse> Token
   * @throws {ApiError} 
   * 
   * @example
   * ```typescript
   * try {
   *   const result = await UserService.login({
   *     mobile: '13800138000',
   *     password: 'Test@123456'
   *   });
   *   console.log('Token:', result.token);
   *   console.log(':', result.user);
   * } catch (error) {
   *   console.error(':', error.message);
   * }
   * ```
   */
  static async login(data: LoginRequest): Promise<LoginResponse> {
    const response = await HttpClient.post<LoginResponse>(
      '/user/login',
      data,
      {
        requireAuth: false, // 
        showLoading: true,
        showError: true,
      }
    );

    //  Token 
    if (response.token) {
      TokenManager.setToken(response.token);
    }
    if (response.user) {
      this.saveUserInfo(response.user);
    }

    return response;
  }

  /**
   * 
   * @description Token
   * 
   * @example
   * ```typescript
   * UserService.logout();
   * console.log('');
   * ```
   */
  static logout(): void {
    TokenManager.clearAuth();
    this.clearUserInfo();
    // 
    window.dispatchEvent(new CustomEvent('user:logout'));
  }

  /**
   * 
   * @param data - 
   * @returns Promise<void> 
   * @throws {ApiError} 
   * 
   * @example
   * ```typescript
   * try {
   *   await UserService.changePassword({
   *     mobile: '13800138000',
   *     old_password: 'OldPass@123',
   *     new_password: 'NewPass@456'
   *   });
   *   console.log('');
   *   // 
   *   UserService.logout();
   * } catch (error) {
   *   console.error(':', error.message);
   * }
   * ```
   */
  static async changePassword(data: ChangePasswordRequest): Promise<void> {
    await HttpClient.post<void>(
      '/user/change-password',
      data,
      {
        requireAuth: true, // 
        showLoading: true,
        showError: true,
      }
    );

    // 
    this.logout();
  }

  /**
   * 
   * @returns Promise<UserInfo> 
   * @throws {ApiError} 
   * 
   * @example
   * ```typescript
   * try {
   *   const userInfo = await UserService.getUserInfo();
   *   console.log(':', userInfo);
   * } catch (error) {
   *   console.error(':', error.message);
   * }
   * ```
   */
  static async getUserInfo(): Promise<UserInfo> {
    const userInfo = await HttpClient.get<UserInfo>(
      '/user/info',
      undefined,
      {
        requireAuth: true, // 
        showLoading: false,
        showError: true,
      }
    );

    // 
    this.saveUserInfo(userInfo);

    return userInfo;
  }

  /**
   * 
   * @description 
   * @returns Promise<UserInfo> 
   * 
   * @example
   * ```typescript
   * const latestUserInfo = await UserService.refreshUserInfo();
   * ```
   */
  static async refreshUserInfo(): Promise<UserInfo> {
    return this.getUserInfo();
  }

  /**
   * 
   * @param userInfo - 
   * 
   * @example
   * ```typescript
   * UserService.saveUserInfo(userInfo);
   * ```
   */
  static saveUserInfo(userInfo: UserInfo): void {
    Storage.set(USER_INFO_KEY, userInfo);
    // 
    window.dispatchEvent(
      new CustomEvent('user:info-updated', { detail: userInfo })
    );
  }

  /**
   * 
   * @returns UserInfo | null  null
   * 
   * @example
   * ```typescript
   * const userInfo = UserService.getLocalUserInfo();
   * if (userInfo) {
   *   console.log(':', userInfo.mobile);
   * } else {
   *   console.log('');
   * }
   * ```
   */
  static getLocalUserInfo(): UserInfo | null {
    return Storage.get<UserInfo>(USER_INFO_KEY);
  }

  /**
   * 
   * 
   * @example
   * ```typescript
   * UserService.clearUserInfo();
   * ```
   */
  static clearUserInfo(): void {
    Storage.remove(USER_INFO_KEY);
  }

  /**
   * 
   * @returns boolean 
   * 
   * @example
   * ```typescript
   * if (UserService.isLoggedIn()) {
   *   console.log('');
   * } else {
   *   console.log('');
   * }
   * ```
   */
  static isLoggedIn(): boolean {
    return TokenManager.isLoggedIn() && !!this.getLocalUserInfo();
  }

  /**
   * 
   * @returns string | null  null
   * 
   * @example
   * ```typescript
   * const mobile = UserService.getCurrentMobile();
   * ```
   */
  static getCurrentMobile(): string | null {
    const userInfo = this.getLocalUserInfo();
    return userInfo?.mobile || null;
  }

  /**
   * ID
   * @returns number | null ID null
   * 
   * @example
   * ```typescript
   * const userId = UserService.getCurrentUserId();
   * ```
   */
  static getCurrentUserId(): number | null {
    const userInfo = this.getLocalUserInfo();
    return userInfo?.id || null;
  }

  /**
   * 
   * @returns boolean 
   * 
   * @example
   * ```typescript
   * if (UserService.isAdmin()) {
   *   console.log('');
   * }
   * ```
   */
  static isAdmin(): boolean {
    const userInfo = this.getLocalUserInfo();
    return userInfo?.user_type === 'admin';
  }

  /**
   * VIP
   * @returns boolean VIP
   * 
   * @example
   * ```typescript
   * if (UserService.isVip()) {
   *   console.log('VIP');
   * }
   * ```
   */
  static isVip(): boolean {
    const userInfo = this.getLocalUserInfo();
    return userInfo?.user_type === 'vip';
  }

  /**
   * 
   * @returns boolean 1-
   * 
   * @example
   * ```typescript
   * if (!UserService.isUserActive()) {
   *   console.log('');
   * }
   * ```
   */
  static isUserActive(): boolean {
    const userInfo = this.getLocalUserInfo();
    return userInfo?.user_status === 1;
  }
}

/**
 * 
 */
export default UserService;
