/**
 * API 
 */
export const API_CONFIG = {
  BASE_URL: 'http://localhost:8080',
  API_PREFIX: '/api/v1',
  TIMEOUT: 30000, // 30
} as const;

/**
 * API
 * @param path - API
 * @returns API URL
 */
export const getApiUrl = (path: string): string => {
  const cleanPath = path.startsWith('/') ? path : `/${path}`;
  return `${API_CONFIG.BASE_URL}${API_CONFIG.API_PREFIX}${cleanPath}`;
};

