/**
 * API 
 */
export enum ErrorCode {
  /**  */
  SUCCESS = 0,
  /**  */
  ERROR = 1,
  /**  */
  INVALID_PARAMS = 400,
  /**  */
  UNAUTHORIZED = 401,
  /**  */
  FORBIDDEN = 403,
  /**  */
  NOT_FOUND = 404,
  /**  */
  SERVER_ERROR = 500,
  /**  */
  DATABASE_ERROR = 501,
  /**  */
  PHONE_EXISTS = 1001,
  /**  */
  PHONE_NOT_FOUND = 1002,
  /**  */
  PASSWORD_ERROR = 1003,
  /**  */
  PASSWORD_WEAK = 1004,
  /**  */
  PHONE_INVALID = 1005,
  /** Token  */
  TOKEN_INVALID = 1006,
  /** Token  */
  TOKEN_EXPIRED = 1007,
  /**  */
  TEMPLATE_NOT_FOUND = 2001,
  /**  */
  TEMPLATE_NO_AUTH = 2002,
}

/**
 * 
 */
export const ERROR_MESSAGES: Record<ErrorCode, string> = {
  [ErrorCode.SUCCESS]: '',
  [ErrorCode.ERROR]: '',
  [ErrorCode.INVALID_PARAMS]: '',
  [ErrorCode.UNAUTHORIZED]: '',
  [ErrorCode.FORBIDDEN]: '',
  [ErrorCode.NOT_FOUND]: '',
  [ErrorCode.SERVER_ERROR]: '',
  [ErrorCode.DATABASE_ERROR]: '',
  [ErrorCode.PHONE_EXISTS]: '',
  [ErrorCode.PHONE_NOT_FOUND]: '',
  [ErrorCode.PASSWORD_ERROR]: '',
  [ErrorCode.PASSWORD_WEAK]: '',
  [ErrorCode.PHONE_INVALID]: '',
  [ErrorCode.TOKEN_INVALID]: 'Token ',
  [ErrorCode.TOKEN_EXPIRED]: 'Token ',
  [ErrorCode.TEMPLATE_NOT_FOUND]: '',
  [ErrorCode.TEMPLATE_NO_AUTH]: '',
};

/**
 * 
 * @param code - 
 * @param defaultMessage - 
 * @returns 
 */
export const getErrorMessage = (
  code: number,
  defaultMessage?: string
): string => {
  return (
    ERROR_MESSAGES[code as ErrorCode] ||
    defaultMessage ||
    ERROR_MESSAGES[ErrorCode.ERROR]
  );
};

/**
 * API 
 */
export class ApiError extends Error {
  /**  */
  code: number;
  /**  */
  response?: any;

  constructor(code: number, message: string, response?: any) {
    super(message);
    this.name = 'ApiError';
    this.code = code;
    this.response = response;
  }

  /**
   * 
   */
  isAuthError(): boolean {
    return (
      this.code === ErrorCode.UNAUTHORIZED ||
      this.code === ErrorCode.TOKEN_INVALID ||
      this.code === ErrorCode.TOKEN_EXPIRED
    );
  }

  /**
   * 
   */
  isForbiddenError(): boolean {
    return this.code === ErrorCode.FORBIDDEN;
  }

  /**
   * 
   */
  isServerError(): boolean {
    return this.code >= 500;
  }
}

