/**
 * API Provider
 * @description API Provider
 */

import HttpClient from './auth';

/**
 * API Provider
 */
export interface APIProviderData {
  /** Provider */
  name: string;
  /** API */
  api_key: string;
  /** API */
  api_url: string;
  /**  */
  api_model: string;
  /** APIv1 */
  api_version?: string;
  /** 0-1- (TINYINT(1)) */
  api_open?: number;
  /**  */
  api_remark?: string;
}

/**
 * API Provider
 */
export interface APIProvider {
  /** Provider ID */
  id: number;
  /**  */
  mobile: string;
  /** Provider */
  name: string;
  /** API */
  api_key_mask: string;
  /** API */
  api_url: string;
  /**  */
  api_model: string;
  /** API */
  api_version: string;
  /** 1-0- (TINYINT(1)) */
  api_status: number;
  /** 0-1- (TINYINT(1)) */
  api_open: number;
  /**  */
  api_remark?: string;
  /**  */
  created_at: string;
  /**  */
  updated_at: string;
}

/**
 * API Provider
 */
export interface APIProviderUpdateData {
  /** Provider */
  name?: string;
  /** API */
  api_key?: string;
  /** API */
  api_url?: string;
  /**  */
  api_model?: string;
  /** API */
  api_version?: string;
  /** 1-0- (TINYINT(1)) */
  api_status?: number;
  /** 0-1- (TINYINT(1)) */
  api_open?: number;
  /**  */
  api_remark?: string;
}

/**
 * API Provider
 */
export interface APIProviderQueryParams {
  /** 1-0- */
  status?: number;
}

/**
 * API Provider
 */
export interface APIProviderListResponse {
  /**  */
  total: number;
  /** Provider */
  list: APIProvider[];
}

/**
 * APIapi_type
 * @deprecated 
 */
export enum APIType {
  /** OpenAIAPI */
  OPENAI = 'openai',
  /** DeepSeek */
  DEEPSEEK = 'deepseek',
  /** Ollama */
  OLLAMA = 'ollama',
  /** OpenAI */
  CUSTOM = 'custom',
}

/**
 * API Provider
 */
export enum APIProviderStatus {
  /**  */
  DISABLED = 0,
  /**  */
  ENABLED = 1,
}

/**
 * API Provider
 */
export enum APIProviderOpenType {
  /**  */
  PRIVATE = 0,
  /**  */
  PUBLIC = 1,
}

/**
 * APIapi_type
 * @deprecated 
 */
export interface APITypeConfig {
  /**  */
  type: APIType;
  /**  */
  label: string;
  /** URL */
  defaultURL: string;
  /**  */
  models: string[];
  /**  */
  description: string;
}

