import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { UserService } from '../services';
import '../styles/login.css';
import Toast from './Toast';

/**
 * 
 */
interface RegisterProps {
  /**  */
  visible: boolean;
  /**  */
  onClose: () => void;
  /**  */
  onSuccess?: () => void;
}

/**
 * 
 * @description +
 */
const Register: React.FC<RegisterProps> = ({ visible, onClose, onSuccess }) => {
  const navigate = useNavigate();
  const [mobile, setMobile] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState<{
    mobile?: string;
    password?: string;
    confirmPassword?: string;
    general?: string;
  }>({});
  const [toast, setToast] = useState<{ message: string; type: 'success' | 'error' | 'warning' | 'info' } | null>(null);

  /**
   * 
   */
  const validateMobile = (value: string): string | undefined => {
    if (!value) {
      return '';
    }
    if (!/^1[3-9]\d{9}$/.test(value)) {
      return '';
    }
    return undefined;
  };

  /**
   * 
   */
  const validatePassword = (value: string): string | undefined => {
    if (!value) {
      return '';
    }
    if (value.length < 8 || value.length > 16) {
      return '8-16';
    }
    // 
    const hasUpperCase = /[A-Z]/.test(value);
    const hasLowerCase = /[a-z]/.test(value);
    const hasNumber = /\d/.test(value);
    const hasSpecialChar = /[!@#$%^&*]/.test(value);

    if (!hasUpperCase || !hasLowerCase || !hasNumber || !hasSpecialChar) {
      return '';
    }
    return undefined;
  };

  /**
   * 
   */
  const validateConfirmPassword = (value: string): string | undefined => {
    if (!value) {
      return '';
    }
    if (value !== password) {
      return '';
    }
    return undefined;
  };

  /**
   * 
   */
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    // 
    const mobileError = validateMobile(mobile);
    const passwordError = validatePassword(password);
    const confirmPasswordError = validateConfirmPassword(confirmPassword);

    if (mobileError || passwordError || confirmPasswordError) {
      setErrors({
        mobile: mobileError,
        password: passwordError,
        confirmPassword: confirmPasswordError,
      });
      return;
    }

    // 
    setErrors({});
    setLoading(true);

    try {
      // API
      await UserService.register({
        mobile: mobile.trim(),
        password: password,
      });

      // 
      setMobile('');
      setPassword('');
      setConfirmPassword('');
      
      // 
      setToast({ message: '', type: 'success' });
      
      // 
      setTimeout(() => {
        // 
        if (onSuccess) {
          onSuccess();
        }
        
        // 
        onClose();

        // 
        navigate('/template');
      }, 2000);
    } catch (error: any) {
      //  - Toast
      setToast({ message: error.message || '', type: 'error' });
    } finally {
      setLoading(false);
    }
  };

  /**
   * 
   */
  const handleMobileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setMobile(value);
    
    // 
    if (errors.mobile) {
      setErrors((prev) => ({
        ...prev,
        mobile: validateMobile(value),
      }));
    }
  };

  /**
   * 
   */
  const handlePasswordChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setPassword(value);
    
    // 
    if (errors.password) {
      setErrors((prev) => ({
        ...prev,
        password: validatePassword(value),
      }));
    }
    
    // 
    if (confirmPassword) {
      setErrors((prev) => ({
        ...prev,
        confirmPassword: value !== confirmPassword ? '' : undefined,
      }));
    }
  };

  /**
   * 
   */
  const handleConfirmPasswordChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setConfirmPassword(value);
    
    // 
    if (errors.confirmPassword) {
      setErrors((prev) => ({
        ...prev,
        confirmPassword: validateConfirmPassword(value),
      }));
    }
  };

  /**
   * 
   */
  const handleBackdropClick = (e: React.MouseEvent) => {
    if (e.target === e.currentTarget) {
      onClose();
    }
  };

  // 
  if (!visible) {
    return null;
  }

  return (
    <div className="login-modal" onClick={handleBackdropClick}>
      <div className="login-content">
        <div className="login-header">
          <h2 className="login-title"></h2>
          <button
            className="login-close-button"
            onClick={onClose}
            aria-label=""
          >
            
          </button>
        </div>

        <form className="login-form" onSubmit={handleSubmit}>
          {/*  */}
          {errors.general && (
            <div className="login-error-banner">
              <span className="error-icon"></span>
              {errors.general}
            </div>
          )}

          {/*  */}
          <div className="login-form-group">
            <div className="login-input-wrapper">
              <span className="login-input-icon"></span>
              <input
                type="tel"
                className={`login-input ${errors.mobile ? 'error' : ''}`}
                placeholder=""
                value={mobile}
                onChange={handleMobileChange}
                maxLength={11}
                autoComplete="tel"
                disabled={loading}
              />
            </div>
            {errors.mobile && (
              <div className="login-error-message">{errors.mobile}</div>
            )}
          </div>

          {/*  */}
          <div className="login-form-group">
            <div className="login-input-wrapper">
              <span className="login-input-icon"></span>
              <input
                type="password"
                className={`login-input ${errors.password ? 'error' : ''}`}
                placeholder=""
                value={password}
                onChange={handlePasswordChange}
                maxLength={16}
                autoComplete="new-password"
                disabled={loading}
              />
            </div>
            {errors.password && (
              <div className="login-error-message">{errors.password}</div>
            )}
          </div>

          {/*  */}
          <div className="login-form-group">
            <div className="login-input-wrapper">
              <span className="login-input-icon"></span>
              <input
                type="password"
                className={`login-input ${errors.confirmPassword ? 'error' : ''}`}
                placeholder=""
                value={confirmPassword}
                onChange={handleConfirmPasswordChange}
                maxLength={16}
                autoComplete="new-password"
                disabled={loading}
              />
            </div>
            {errors.confirmPassword && (
              <div className="login-error-message">{errors.confirmPassword}</div>
            )}
          </div>

          {/*  */}
          <button
            type="submit"
            className="login-submit-button"
            disabled={loading}
          >
            {loading ? '...' : ''}
          </button>

          {/*  */}
          <div className="login-footer">
            <p className="login-hint">
              8-16
            </p>
          </div>
        </form>
      </div>
      {toast && (
        <Toast
          message={toast.message}
          type={toast.type}
          duration={2000}
          onClose={() => setToast(null)}
        />
      )}
    </div>
  );
};

export default Register;
